
import React, { useRef } from 'react'
import {  useFrame} from '@react-three/fiber'
import { useGLTF } from '@react-three/drei'
import * as THREE from 'three';


export default function Model(props) {
  const group = useRef()
  const { nodes, materials } = useGLTF('/scene-draco.glb')
  useFrame(() => (group.current.rotation.y += 0.0008))
  let material1 = new THREE.MeshBasicMaterial( { color: 0x0a2360, wireframe: false } );
  let material4 = new THREE.MeshBasicMaterial( { color: 0x1533ab, wireframe: false } );

  return (
    <group ref={group} scale={[0.002, 0.002, 0.002]} position={[0, 0, -100]} dispose={null}>
      <group rotation={[-Math.PI / 2, 0, 0]}>
        <group position={[-102253.52, -210688.86, -17050.52]}>
          <mesh material={material1} geometry={nodes.mesh_0.geometry} />
          <mesh material={material1} geometry={nodes.mesh_1.geometry} />
          <mesh material={material1} geometry={nodes.mesh_2.geometry} />
          <mesh material={material1} geometry={nodes.mesh_3.geometry} />
        </group>
      </group>
    </group>
  )
}
